body {
  margin: 0;
  font-family: Arial;
}

.people {
  grid-template-columns: repeat(2, 220px);
  justify-content: center;
  gap: 0 20px;
  padding-top: 30px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 220px);
  }
}

.person {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition: transform 1s;
  display: flex;
}

.person:hover {
  z-index: 999;
  background-color: #eee;
  border-radius: 10px;
  position: relative;
  transform: scale(1.3);
}

.person:hover .person__extra {
  visibility: visible;
}

.person:hover .person__photo > img {
  border-radius: 60px;
}

.person__photo {
  max-width: 200px;
  position: relative;
}

.person__photo > img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: all 1s;
}

.person__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
  position: absolute;
  top: calc(50% - 16px);
  right: 10px;
}

.person__details {
  text-align: center;
}

.person__name {
  color: #2f4f4f;
  text-align: center;
  font-family: Arial;
  font-size: 20px;
  font-weight: 700;
}

.person__extra {
  visibility: hidden;
  text-align: center;
  color: #000;
  margin-top: 9px;
  font-family: Arial;
  font-size: 18px;
  font-weight: 400;
}
